import {repoApiGet} from '@/assets/js/baseApi'
import {getRepoServerApi} from "../restfulapi/serverConfigApi"

let getRepoWinPkgsApi = function(){
    return new Promise((resolve, reject) => {
        getRepoServerApi().then((repoUrl) => {
            let getRepoAppUrl =  repoUrl + "/repoexe";
            repoApiGet(getRepoAppUrl, "").then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
    })
}

let getRepoWinPkgsByTypeApi = function(type){
    return new Promise((resolve, reject) => {
        let data = {
            type
        }
        getRepoServerApi().then((repoUrl) => {
            let getRepoAppUrl =  repoUrl + "/exeByType";
            repoApiGet(getRepoAppUrl, data).then((data) => {
                resolve(data);
            }).catch((error) => {
                resolve(error.response);
            })
        })
    })
}
export {
    getRepoWinPkgsApi,
    getRepoWinPkgsByTypeApi
}


