<template>
    <div v-show="!isSingleMode" class="pkgStatusContent p-r">
        <div class="search" v-show="isShowSearchInput"> 
            <el-input size="small" class="w-250 m-l-10" v-model="keywords" :placeholder="$t('winPkgControl.keywordHolder')" >
            </el-input>
            <i class="fa fa-search search-icon" aria-hidden="true"></i>
        </div>

        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="winMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="true"></i>
                    {{$t('winPkgControl.exePkgMarket')}}
                </span>
                <el-table
                    :data="exeTableData.filter(data => !keywords || data.projectname.toLowerCase().includes(keywords.toLowerCase()))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img :src="getSrc(scope.row.reid)" :onerror="defaultImg" alt="no img" style="width:52px"/>
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="projectname"
                    :label="$t('winPkgControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>

                    <el-table-column
                    prop="version"
                    :label="$t('winPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>
                    <el-table-column
                    prop="type"
                    :label="$t('winPkgControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('winPkgControl.uninstall'), value: 'uninstall' }, { text: $t('winPkgControl.install'), value: 'install' }, { text: $t('winPkgControl.upgrade'), value: 'upgrade' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="selectBatchinstallPkg('uninstallExe', scope.row)"
                                        v-if="scope.row.type === 'uninstall'"
                                        :disabled="scope.row.type === 'uninstall'&&!scope.row.tool"
                                        >
                                            {{$t('winPkgControl.uninstall')}}
                                        </el-button>
                                        
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="selectBatchinstallPkg('upgradeExe', scope.row)" 
                                        v-if="scope.row.type === 'upgrade'"
                                        >
                                            {{$t('winPkgControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="selectBatchinstallPkg('installExe', scope.row)" 
                                        v-if="scope.row.type === 'install'"
                                        >
                                            {{$t('winPkgControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane name="zipMarket">
                <span slot="label">
                    <i class="fa fa-sellsy m-r-5" aria-hidden="listLoading"></i>
                    {{$t('winPkgControl.compressedPkgMarket')}}
                </span>
                <el-table
                    :data="pkgTableData.filter(data => (!keywords || data.projectname.toLowerCase().includes(keywords.toLowerCase())))"
                    tooltip-effect="dark"
                    style="width: 100%"
                    v-loading="listLoading"
                    :header-cell-style="{background:'#fafafa',color:'#606266'}"
                    >

                    <el-table-column
                    label=""
                    width="100"
                    >
                        <template slot-scope="scope">
                            <div>
                                <img src="@/assets/imgs/package.png"  alt="no img" style="width:48px" />
                            </div>
                        </template>
                    </el-table-column>
                    <el-table-column
                    prop="filename"
                    :label="$t('linuxPkgControl.fileName')"
                    min-width="120"
                    >
                    </el-table-column>
                    <el-table-column
                    prop="projectname"
                    :label="$t('winPkgControl.projectName')"
                    min-width="120"
                    sortable
                    >
                    </el-table-column>
                    <el-table-column
                    prop="version"
                    :label="$t('winPkgControl.version')"
                    min-width="120"
                    sortable>
                    </el-table-column>

                    <el-table-column
                    prop="type"
                    :label="$t('winPkgControl.action')"
                    min-width="120"
                    :filters="[{ text: $t('winPkgControl.uninstall'), value: 'uninstall' }, { text: $t('winPkgControl.install'), value: 'install' }, { text: $t('winPkgControl.upgrade'), value: 'upgrade' }]"
                    :filter-method="filterType"
                    filter-placement="bottom-end"
                    >
                        <template slot-scope="scope">
                            <div>
                                <span>
                                    <el-button-group>
                                        <el-button 
                                        size="small" 
                                        type="danger" 
                                        @click="selectBatchinstallPkg('uninstallPkg', scope.row)"
                                        v-if="scope.row.type === 'uninstall'"
                                        >
                                            {{$t('winPkgControl.uninstall')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="warning" 
                                        @click="selectBatchinstallPkg('upgradePkg', scope.row)" 
                                        v-if="scope.row.type === 'upgrade'"
                                        >
                                            {{$t('winPkgControl.upgrade')}}
                                        </el-button>
                                        <el-button 
                                        size="small" 
                                        type="primary" 
                                        @click="selectBatchinstallPkg('installPkg', scope.row)" 
                                        v-if="scope.row.type === 'install'"
                                        >
                                            {{$t('winPkgControl.install')}}
                                        </el-button>
                                    </el-button-group>
                                </span>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </el-tab-pane>
            <el-tab-pane name="winStatusHistory">
                <span slot="label">
                    <i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('winPkgControl.winManagerHistory')}}
                </span>
                <pkg-history
                :statusType="batchWinPkgType"
                ref="pkgHistory"
                @getStatusCount="getBatchAppStatusCount"
                :groupname="groupname"
                @isFinished="isFinished"
                >
                </pkg-history>

            </el-tab-pane>
        </el-tabs>
        <el-dialog :visible.sync="dialogTasknameVisible" @close="initPkgInfo()" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('winPkgControl.batchInstallPkg')}}
            </div>
            <el-form :model="pkgStatusForm" :rules="rules" ref="pkgStatusForm">
                <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                    <el-input v-model.trim="pkgStatusForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('winPkgControl.installPkgTaskName')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="batchinstallPkg()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <div class="appStatusCount" v-show="isShowOverview">
            <span>{{$t('label.taskName')}} {{win_taskname}}</span>
            <span>{{$t('winPkgControl.total')}} {{total}}</span>
            <span>{{$t('winPkgControl.deploying')}} {{installing}}</span>
            <span>{{$t('winPkgControl.success')}} {{success}}</span>
            <span>{{$t('winPkgControl.failed')}} {{failed}}</span>
        </div>
    </div>
</template>

<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .pkgStatusContent{
        position: relative;
        .appStatusCount{
            position: absolute;
            top: 10px;
            right: 6px;
            span{
                font-weight: 500;
                font-size: 11px;
                &:nth-child(1){
                    color: $primary-color;
                }
                &:nth-child(2){
                    border-left: 2px solid #ddd;
                    padding-left: 3px; 
                    color: $info-color;
                }
                &:nth-child(3){
                    color: $warn-color;
                }
                &:nth-child(4){
                    color: $success-color;
                }
                &:nth-child(5){
                    color: $darkgray-color;
                }
            }
            
        }
    }

    .search{
        position: absolute;
        top: 3px;
        right: 15px;
        width: 230px;
        z-index: 1001;
        .search-icon{
            position: absolute;
            top: 6px;
            right: 1px;
            color: #ddd;
            z-index: 10001;
        }
    }
    .noData{
        margin-top: 30px;
        p{
            img{
                width: 27px;
                vertical-align: middle;
            }
            margin-bottom: 20px;
            color: #bfbfbf;
            font-size: 21px;
        }
    }

</style>

<script>
    import {winPkgControl, androidControl} from '@/assets/js/lwm2mMap'
    import handleResponse from '@/components/restfulapi/handleResponse'
    import {showSystemAppArray, disableAppArray} from "@/assets/js/constant"
    import {getRepoWinPkgsApi, getRepoWinPkgsByTypeApi} from '../../../components/restfulapi/getRepoWinPackageApi';
    import {getRepoPkgsApi, getRepoApi} from '@/components/restfulapi/getRepoAppApi'
    import{getAppStatusOverviewApi} from "@/components/restfulapi/appStatusApi"

    import {getDeviceStatus, setDeviceStatus, batchExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import {getRepoSrcApi} from "../../restfulapi/serverConfigApi"

    import {singleWinPkgType, batchWinPkgType, winPkgExe, winPkgZip} from "../../../assets/js/constant";
    import pkgHistory from "./history"
    export default{
        name: 'batchwinPkgControl',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "winMarket",
                pkgTableData: [],
                exeTableData: [],
                defaultImg: 'this.src="' + 'static/imgs/default_windows.png' + '"',
                dialogTasknameVisible: false,
                //win package status history
                projectname: '',
                version: '',
                address: '',
                tool: "",
                filename: '',
                //batch installPkg
                pkgStatusForm:{
                    taskname: '',
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },

                batchWinPkgType: batchWinPkgType,
                cid: "",
                disableAppArray: disableAppArray,
                //installstatus count show
                isShowOverview: false,
                win_taskname: '',
                success: 0,
                installing: 0,
                failed: 0,
                listLoading: false,
                keywords: '',
                zipKeywords: ""
            }
        },

        props:{
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        components: {
            pkgHistory,
        },

        methods:{
            filterType(value, row){
                return row.type === value ;
            },

            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }

                this.listLoading = true;
                getDeviceStatus(this.selectedAgentsData[0].value, winPkgControl["getExeInfo"]).then((obj) => {
                    let pkgManagementInfoArray = [];
                    let devicePkgArray = [];
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            if(res.content.value){
                                let allPkgData = JSON.parse(res.content.value);
                                // let allPkgData = apppackageinfo.data;
                                if(Object.prototype.toString.call(allPkgData) === "[object Array]" && allPkgData.length > 0){
                                    allPkgData.forEach((val) => {
                                        let uninstallPkgObj = {
                                            type: 'uninstall',
                                            filename: "",
                                            projectname: val.projectname,
                                            tool: "",
                                            version: val.version,
                                            address: "",
                                            reid: "",
                                        };
                                        pkgManagementInfoArray.push(uninstallPkgObj);
                                        devicePkgArray.push(uninstallPkgObj);                            
                                            
                                    })
                                }
                            }
                            
                            this.getRepoPkgs(pkgManagementInfoArray, devicePkgArray, winPkgExe);  
                        }
                    })   
                })

                getDeviceStatus(this.selectedAgentsData[0].value, winPkgControl["getPkgInfo"]).then((obj) => {
                    let pkgManagementInfoArray = [];
                    let devicePkgArray = [];
                    this.listLoading = false;
                    handleResponse(obj, (res) => {
                        if(res.status == "CONTENT"){
                            let allPkgData = JSON.parse(res.content.value);
                            if(Object.prototype.toString.call(allPkgData) === "[object Array]" && allPkgData.length > 0){
                                allPkgData.forEach((val) => {
                                    let uninstallObj = {
                                        type: 'uninstall',
                                        filename: val.pkgname,
                                        projectname: val.appname,
                                        tool: "",
                                        version: val.version,
                                        address: "",
                                        reid: "",
                                    };
                                    pkgManagementInfoArray.push(uninstallObj);
                                    devicePkgArray.push(uninstallObj);  
                                })
                            }
                            this.getRepoPkgs(pkgManagementInfoArray, devicePkgArray, winPkgZip);  
                        }
                    })   
                })
            },
            
            // Deletes an object of a particular value in array
            removeObjInArray(originData, rem_pkg_val, version){
                let res = false;
                if(Object.prototype.toString.call(originData) === "[object Array]"){
                    
                    originData.forEach((obj_val, index) => {
                        if(obj_val.projectname === rem_pkg_val&& obj_val.version === version){
                            originData.splice(index ,1)
                            res = true;
                        }
                    })
                }else{
                     console.error("data is not array")
                     
                }
                return res;
            },
            //get repo apps
            getRepoPkgs(pkgManagementInfoArray, devicePkgArray, type){
                if(
                    Object.prototype.toString.call(pkgManagementInfoArray) != "[object Array]" ||
                    Object.prototype.toString.call(devicePkgArray) != "[object Array]"
                ){
                    console.error("getRepoPkgsparameter is not array")
                    return;
                }
                let token;
                let installPkgManagementInfo = {};
                let upgradePkgManagementInfo = {};
                this.listLoading = true;
                getRepoWinPkgsByTypeApi(type).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                       if(res.status === "success"){
                            let installPkgopt = "";
                            let upgradePkgopt = "";
                            let lastVersionCode = [];
                            if(Object.prototype.toString.call(res.data) === "[object Array]"){
                                res.data.forEach((val) => {
                                    
                                    installPkgManagementInfo = {
                                        type : "install", 
                                        filename: val.filename, 
                                        projectname: val.projectname, 
                                        tool: val.tool,
                                        version: val.version, 
                                        address: val.address,
                                        reid: val.reid
                                        };
                                    pkgManagementInfoArray.push(installPkgManagementInfo);
                                    console.log(devicePkgArray)
                                    devicePkgArray.forEach((deviceapp_val, index) => {
                                        if((type == winPkgExe&& deviceapp_val.projectname.indexOf(val.projectname) != -1)||
                                        (type == winPkgZip&& deviceapp_val.projectname == val.projectname)){
                                            // pop latest app info
                                            pkgManagementInfoArray.pop();
                                            console.log(pkgManagementInfoArray)
                                            devicePkgArray[index].address = val.address;
                                            devicePkgArray[index].tool = val.tool;
                                            if(this.removeObjInArray(pkgManagementInfoArray, deviceapp_val.projectname, deviceapp_val.version)){
                                                pkgManagementInfoArray.push(deviceapp_val)
                                            }

                                            if (_g.cpr_version(val.version, deviceapp_val.version)){
                                                upgradePkgManagementInfo = {
                                                    type : "upgrade", 
                                                    filename: val.filename, 
                                                    projectname: val.projectname, 
                                                    productname: deviceapp_val.projectname,
                                                    version: val.version, 
                                                    tool: val.tool,
                                                    address: val.address,
                                                    reid: val.reid
                                                    };
                                                    //push update app 
                                                pkgManagementInfoArray.push(upgradePkgManagementInfo);     
                                                
                                            }
                                            
                                        }
                                    })
                                })
                            }
                        }
                        if(type == winPkgExe){
                            this.exeTableData = pkgManagementInfoArray.reverse();
                        }
                        if(type == winPkgZip){
                            this.pkgTableData = pkgManagementInfoArray.reverse();
                        }
                    })
                    
                })
            },
	
            //batch install app
            batchSetAppSensor(cid, setSensorVal){
                if(winPkgControl[cid] === undefined){
                    this.$swal("", this.$t('global.functionNotSupport'), 'info', {button: this.$t('global.confirm')});
                    return;
                }
                _g.openGlobalLoading();
               
                let pkgdata = {
                    groupname: this.groupname,
                    taskname: this.pkgStatusForm.taskname,
                    projectname: this.projectname,
                    tool: this.tool,
                    version: this.version,
                    downloadpath: setSensorVal,

                }  
                let taskname = this.pkgStatusForm.taskname;
                this.dialogTasknameVisible = false;                  
                batchExecDeviceStatus(winPkgControl[cid], pkgdata).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status == "CHANGED"){
                            //show overview
                            setLocal("win_taskname", taskname);
                            this.activeName = "winStatusHistory";
                            this.$refs.pkgHistory.intervalGetPkgStatusHistory();
                        }else{
                            _g.handleError(res);
                        }
                    })  
                })                    
            },

            batchinstallPkg(){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("", this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')});
                    return;
                }
                this.$refs.pkgStatusForm.validate((valid) => {
                    if(valid) {
                        let apkDownloadAddress = this.address + "/" + this.filename;
                        this.batchSetAppSensor(this.cid, apkDownloadAddress);
                        
                    }
                })
            },
            
            selectBatchinstallPkg(type, selectedAppData){
                this.cid = type;
                this.address = selectedAppData.address;
                if(type == "upgradeExe"){
                    this.projectname = selectedAppData.productname;
                }else{
                    this.projectname = selectedAppData.projectname;
                }
                this.tool = selectedAppData.tool;
                this.filename = selectedAppData.filename;
                this.version = selectedAppData.version;
                this.pkgStatusForm.taskname = "";
                this.dialogTasknameVisible = true;
            },

            initPkgInfo(){
                this.projectname = "";
                this.version = "";
                this.address = "";
                this.pkgStatusForm.taskname = "";
                this.cid = "";
                this.tool = "";
                this.filename = "";
            },

             //get updatedp status count
            getBatchAppStatusCount(){
                let win_taskname = getLocal("win_taskname");
                if(!win_taskname){
                    console.error("getBatchAppStatusCount:localStorage(win_taskname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getAppStatusOverviewApi(win_taskname, batchWinPkgType).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.win_taskname = win_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName === "winStatusHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },
            
            initData(){
                this.pkgTableData = [];
            },

            getSrc(reid){
                if(reid){
                    let repourl =  getRepoSrcApi();
                    return repourl+"/getExeIcon/"+reid;
                }else{
                    return 'static/imgs/default_windows.png';
                }
            },

            handleClick(tab){
                if(tab.name === "winStatusHistory"){
                    this.getBatchAppStatusCount();
                }else{
                    this.isShowOverview = false;
                }
            },
            isFinished(){
                this.getSensorStatus();  
            },

        },
       
        computed: {
            
            isShowSearchInput(){
                if(this.activeName === "winMarket"|| this.activeName === "zipMarket"){
                    return true;
                }else{
                    return false;
                }
            },
            total(){
                return  this.success+ this.installing+ this.failed;
            }

        },
       
        watch: {
            isSingleMode(val){
                if(!val){
                    this.initData();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.initData();
                    this.getSensorStatus();
                }
            },
            activeName(){
                this.keywords = "";
            }
        }

    }
</script>


